.product-info {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.seller-info-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.seller-name {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  font-size: 14px;
}

.verified-badge {
  color: var(--meli-blue);
  font-weight: bold;
}

.product-status {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
}

.status-badge.new {
  background-color: var(--meli-green);
  color: white;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 11px;
  font-weight: 600;
}

.sold-count {
  color: var(--meli-gray);
}

.product-title {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.3;
  color: var(--meli-dark-gray);
  margin: 0;
}

.product-rating {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
}

.stars {
  display: flex;
  gap: 2px;
}

.star {
  color: #ddd;
  font-size: 16px;
}

.star.filled {
  color: #ffd700;
}

.rating-value {
  font-weight: 600;
  font-size: 14px;
}

.reviews-count {
  color: var(--meli-gray);
  font-size: 12px;
}

.price-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 16px;
}

.price-info {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.current-price {
  font-size: 32px;
  font-weight: 600;
  color: var(--meli-dark-gray);
}

.original-price {
  font-size: 18px;
  color: var(--meli-gray);
  text-decoration: line-through;
}

.discount-badge {
  background-color: var(--meli-green);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
}

.installment-info {
  font-size: 14px;
  color: var(--meli-gray);
}

.promotions {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.promotion-badge {
  background-color: var(--meli-blue);
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
  display: inline-block;
  width: fit-content;
}

.color-selection {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.color-label {
  font-size: 14px;
  color: var(--meli-dark-gray);
}

.color-swatch {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid #ddd;
}

.product-highlights {
  margin-bottom: 24px;
}

.highlights-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--meli-dark-gray);
}

.highlights-list {
  list-style: none;
  padding: 0;
  margin-bottom: 12px;
}

.highlight-item {
  padding: 8px 0;
  border-bottom: 1px solid #f0f0f0;
  font-size: 14px;
  color: var(--meli-dark-gray);
}

.highlight-item:last-child {
  border-bottom: none;
}

.payment-methods-preview {
  margin-top: 24px;
}

.payment-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--meli-dark-gray);
}

.payment-banner {
  background-color: var(--meli-green);
  color: white;
  padding: 12px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 16px;
}

.payment-cards {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 12px;
}

.payment-type {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.payment-label {
  font-size: 14px;
  font-weight: 600;
  color: var(--meli-dark-gray);
}

.card-logos {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.card-logo {
  background-color: #f8f9fa;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
  color: var(--meli-dark-gray);
  border: 1px solid #e6e6e6;
}

.payment-note {
  font-size: 12px;
  color: var(--meli-gray);
  font-style: italic;
}

@media (max-width: 768px) {
  .product-title {
    font-size: 20px;
  }
  
  .current-price {
    font-size: 28px;
  }
  
  .price-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .seller-info-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
} 