.product-specifications {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.specifications-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--meli-dark-gray);
  margin: 0;
}

.specifications-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.specification-item {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 16px;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.specification-header {
  display: flex;
  align-items: center;
  gap: 12px;
}

.specification-icon {
  font-size: 18px;
}

.specification-label {
  font-size: 14px;
  font-weight: 600;
  color: var(--meli-dark-gray);
}

.specification-detail {
  font-size: 12px;
  color: var(--meli-gray);
  margin-left: 30px;
}

.screen-size-slider {
  margin-left: 30px;
  margin-top: 8px;
}

.slider-track {
  position: relative;
  height: 8px;
  background-color: #e6e6e6;
  border-radius: 4px;
  margin-bottom: 8px;
}

.slider-fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: var(--meli-blue);
  border-radius: 4px;
  transition: width 0.3s ease;
}

.slider-thumb {
  position: absolute;
  top: -4px;
  width: 16px;
  height: 16px;
  background-color: var(--meli-blue);
  border-radius: 50%;
  transform: translateX(-50%);
  transition: left 0.3s ease;
}

.slider-labels {
  display: flex;
  justify-content: space-between;
  font-size: 10px;
  color: var(--meli-gray);
  font-weight: 600;
}

@media (max-width: 768px) {
  .specifications-title {
    font-size: 18px;
  }
  
  .specification-item {
    padding: 12px;
  }
  
  .specification-header {
    gap: 8px;
  }
  
  .specification-label {
    font-size: 13px;
  }
} 