.product-description {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.description-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--meli-dark-gray);
  margin: 0;
}

.description-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.description-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.section-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--meli-dark-gray);
  margin: 0;
}

.section-text {
  font-size: 14px;
  line-height: 1.6;
  color: var(--meli-dark-gray);
  margin: 0;
  text-align: justify;
}

@media (max-width: 768px) {
  .description-title {
    font-size: 18px;
  }
  
  .section-title {
    font-size: 15px;
  }
  
  .section-text {
    font-size: 13px;
  }
} 