* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Proxima Nova', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
  color: #333;
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* MercadoLibre color palette */
:root {
  --meli-yellow: #fff159;
  --meli-blue: #3483fa;
  --meli-green: #00a650;
  --meli-red: #ff4444;
  --meli-gray: #666;
  --meli-light-gray: #f5f5f5;
  --meli-dark-gray: #333;
  --meli-white: #fff;
  --meli-black: #000;
}

/* Utility classes */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.btn {
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-primary {
  background-color: var(--meli-blue);
  color: white;
}

.btn-primary:hover {
  background-color: #2968c8;
}

.btn-secondary {
  background-color: white;
  color: var(--meli-blue);
  border: 1px solid var(--meli-blue);
}

.btn-secondary:hover {
  background-color: var(--meli-blue);
  color: white;
}

.btn-large {
  padding: 16px 32px;
  font-size: 16px;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.mb-1 { margin-bottom: 8px; }
.mb-2 { margin-bottom: 16px; }
.mb-3 { margin-bottom: 24px; }
.mb-4 { margin-bottom: 32px; }

.mt-1 { margin-top: 8px; }
.mt-2 { margin-top: 16px; }
.mt-3 { margin-top: 24px; }
.mt-4 { margin-top: 32px; }

.p-1 { padding: 8px; }
.p-2 { padding: 16px; }
.p-3 { padding: 24px; }
.p-4 { padding: 32px; }

/* Responsive design */
@media (max-width: 768px) {
  .container {
    padding: 0 16px;
  }
  
  .btn-large {
    padding: 14px 28px;
    font-size: 14px;
  }
} 